}

  @media screen and (max-width: 768px) {
    .container{
      padding: 0 16px;
    }
  }
  
  @media(max-width: 768px){
    .headerLeft nav, .headerActions{
      display: none;
    }
    .getDiscount{
      display: block !important;
    }
    .homeBlogs-bottom{
      grid-template-columns: 1fr !important;
    }
    .tabs-headings .tab{
      font-size: 24px !important;
    }
    .cart{
      display: flex;
      flex-direction: column;
    }
    .cartLeft, .cartRight{
      width: 100% !important; 
    }
    .cartOrder-info{
      width: 100% !important;
    }
    .productDetail-head{
      flex-direction: column;
    }
    .productDetail-head_left, .productDetail-head_right{
      width: 100%;
    }
    .productDetail-body{
      flex-direction: column;
    }
    .productDetail-body_left {
      order: 3;
      width: 100% !important;
      padding-top: 0 !important;
      align-items: center;
      overflow-x: scroll;
      flex-direction: row !important;
    }
    .productDetail-body_middle {
      order: 1;
    }
    .productDetail-body_right {
      order: 2;
      padding-top: 0 !important;
      width: 100% !important;
    }
    .productMain-info_detailed-price{
      width: 100% !important;
    }
    .productMain-info_detailed-compound{
      width: 100% !important;
      margin-top: 20px;
    }
    .productDetail-body_left div{
      min-width: 125px;
    }


    footer .container{
      display: flex;
      flex-direction: column;
    }
    .footerCenter {
      order: 1;
      flex-direction: column;
    }
    .footerTop {
      order: 2;
      align-items: flex-start;
      flex-direction: column;
    }
    .footerBottom {
      order: 3;
      flex-direction: column-reverse;
      align-items: flex-start;
    }
    .footerCenter-left, .footerTop-right{
      flex-direction: column;
    }
    .footerBottom-right .footerBottom-menu{
      flex-wrap: wrap;
    }
    
  }